.flex-container {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.matches-emtpy-state {
  font-family: 'Open Sans', sans-serif;
  font-size: 2em;
  color: #216583;
}

.match-img{
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 0px;
  overflow: hidden;
}

.match-img img{
 padding: 0px;
 margin: 0px;
 width: 100%;
}

.match-info {
  text-align: left;
  list-style: none;
  padding: 0px 20px 0 20px;
  margin-top: 0;
}

.match-name {
  font-weight: bolder;
  color: #1d1d1d;
  text-align: left;
  margin: 8px 20px;
}

.match-info li {
  float: none!important;
  margin: 8px 0px;
}

.chat-btn {
  text-decoration: none;
  border-radius: 0;
  margin: auto;
}

.swipe-container {
  height: 100%;
}

.match-control-buttons{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.match-control-chat{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  /* position: fixed;
  bottom: 0px; */
  width: 90%;
  text-decoration: none;
 }

.match-control-accept{
 float: left;
 background-color: transparent;
 height: -webkit-fit-content;
 height: -moz-fit-content;
 height: fit-content;
 border: none;
}

.match-control-decline{
  float: right;
  background-color: transparent;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border: none;
 }

 .btn_WhatsApp {
   background-color: transparant!important;
   width: 100px;
   height: 50px;

 }
 .WhatsApp {

  height: 40px;
  -webkit-animation: WhatsApp-spin infinite 1s linear;
          animation: WhatsApp-spin infinite 1s linear;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;

 }

 @-webkit-keyframes WhatsApp-spin {
  from { -webkit-transform: rotate(-10deg); transform: rotate(-10deg); }
  to { -webkit-transform: rotate(40deg); transform: rotate(40deg); }
}

 @keyframes WhatsApp-spin {
  from { -webkit-transform: rotate(-10deg); transform: rotate(-10deg); }
  to { -webkit-transform: rotate(40deg); transform: rotate(40deg); }
}



